.ranking {
    padding: 0 3px 100px 3px;

    .authority {
        .title {
            font-weight: bold;
            line-height: 35px;
        }

        .am-list {
            .am-list-body::before {
                display: none;
            }
            
            .am-list-brief {
                font-size: 12px;
            }

            .am-list-item {
                background-color: #f3f3f4;
                padding: 3px 0;

                .am-list-thumb {
                    height: 85px;
                    width: 85px;
                    margin-right: 10px;

                    img {
                        height: 100%;
                        width: 100%;
                        border-radius: 5px;
                    }
                }
            }
        }
    }

    .global {
        .title {
            font-weight: bold;
            line-height: 35px;
            margin-top: 15px;
        }

        .am-grid {
            div.am-grid-item-content {
                padding: 0;


                div {
                    height: 100%;
                    padding: 2px;

                    span {
                        position: absolute;
                        bottom: 8px;
                        left: 8px;
                        font-size: 12px;
                        color: #fff;
                    }
                }

            }

            .am-flexbox-item {
                background-color: #f3f3f4;
            }
        }
    }
}